.wrapper {
  margin-top: 30px;
  text-align: center;

  .avator {
    border-radius: 50%;
    width: 64px;
    height: 64px;
  }

  .nickname {
    margin-top: 13px;
    font-family: PingFangSC-Medium;
    font-size: 16px;
    color: #333333;
    letter-spacing: -0.23px;
  }

  .account {
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #8C8C8C;
    letter-spacing: 0;
    text-align: center;
  }
}

@media screen and (min-width: 768px) {
  .wrapper {
    margin-top: 130.6px;  
    .avator {
      width: 90px;
      height: 90px;
    }
  
    .nickname {
      margin-top: 20px;
      font-size: 24px;
      letter-spacing: -0.34px;
    }
  
    .account {
      margin-top: 20px;
      font-size: 16px;
    }
  }
}