@import '../../globalVariable';

$iconSize: 16px;
$fullSize: 100%;
$height: 50px;
$fontSize: 14px;
$borderWidth:1px;
$paddingTop:14px;
$paddingBottom:16px;
$greyColor: #ededed;
$inputInnerHeight:$height - $borderWidth*2 - $paddingTop -$paddingBottom;


.wrapper {
  position: relative;

  .inputWrapper {
    position: relative;
    height: $height;
    @media screen and (min-width: 768px) {
      height: 60px;
    }

    .input {
      width: $fullSize;
      height: $inputInnerHeight;
      padding:$paddingTop 0 $paddingBottom;
      border: $borderWidth solid $greyColor;
      border-radius: 3px;
      text-indent: 20px;
      line-height: $inputInnerHeight;
      font-size: 14px;
      font-family: PingFangSC-Regular;
      @media screen and (min-width: 768px) {
        height: 28px;
        font-size: 18px;
        line-height: 28px;
      }
      
      &::placeholder {
        font-size: $middleFontSize;
        color: #bdbdbd;
      }
      &.inputting {
        text-align: center;
        text-indent: 0;
      }
      &.disabled {
        text-align: center;
        text-indent: 0;
        background-color: $greyColor;
      }
    }
  }

  .tipText {
    margin: 8px 13px;
    font-size: $smallFontSize;
    letter-spacing: -0.09px;
    line-height: 17px;

    &.error {
      color: $errorTipColor;
    }

    &.warning {
      color: #f6bd00;
    }

    &.default, &.correct {
      display: none;
    }
  }
}

