
.wrapper {
    position: relative;
    width: 300px;
    margin: 0 auto;
    .describe {
        margin: 30px 0;
        text-align: center;
    }
    .button {
        margin: 30px 0 0 1px;
    }
    .link {
        margin-top: 30px;
    }
}

@media screen and (min-width: 768px) {
    .wrapper {
        width: 370px;
        .describe {
            margin: 40px 0 64px;
            text-align: center;
        }
        .button {
            margin: 20px 0 0 1px;
        }
        .link {
            margin-top: 50px;
        }
    }
}