@charset "UTF-8";
.rc-notification {
  position: fixed;
  width: 95%;
  margin: 0 2.5%;
  box-sizing: border-box;
  z-index: 1000; }
  .rc-notification-wrapper {
    text-align: center; }
  .rc-notification-notice {
    display: inline-block;
    padding: 13px 55px;
    margin-bottom: 8px;
    background: rgba(0, 0, 0, 0.8);
    border-radius: 30px;
    line-height: 1.5;
    color: #ffffff;
    text-align: center;
    position: relative; }
    @media screen and (min-width: 768px) {
      .rc-notification-notice {
        font-size: 18px;
        padding: 17px 60px; } }
    .rc-notification-notice-closable {
      padding-right: 20px; }
    .rc-notification-notice-close {
      position: absolute;
      right: 5px;
      top: 3px;
      color: #000;
      cursor: pointer;
      outline: none;
      font-size: 16px;
      font-weight: 700;
      line-height: 1;
      text-shadow: 0 1px 0 #fff;
      filter: alpha(opacity=20);
      opacity: .2;
      text-decoration: none; }
      .rc-notification-notice-close-x:after {
        content: '×'; }
      .rc-notification-notice-close:hover {
        opacity: 1;
        filter: alpha(opacity=100);
        text-decoration: none; }

.toast-enter {
  opacity: 0.01;
  transform: translateY(50%); }

.toast-enter-active {
  opacity: 1;
  transform: translateY(0%);
  transition: all 300ms ease-out; }

.toast-exit {
  opacity: 1;
  transform: translateY(0%); }

.toast-exit-active {
  opacity: 0.01;
  transform: translateY(50%);
  transition: all 300ms ease-out; }
