// scss 全局变量
$fontColor: #808083;
$smallestFontSize: 10px;
$smallFontSize: 12px;
$middleFontSize: 14px;
$bigFontSize: 16px;
$inputMarginBottom: 17px;
$yxLightGreen: #2DBE60;
$errorTipColor: #CD5450;

@mixin hide {
  &.hide {
    display: none;
  }
}

@mixin vercodeHelpLinkWrapper {
  .vercodeHelpLinkWrapper {
    margin: 20px 0 0;
    text-align: center;
    @include hide;
  }
}

@mixin wechatButtonWrapper {
  .wechatButtonWrapper {
    margin-top: 16px;
    border: 1px solid #cacaca;
    font-size: $smallFontSize;
    color: #848a8b;
    background: #fff;

    &:hover {
      background: #f8f8f8;
    }
  }
}

@mixin centerLayout {
  width: 380px;
  margin: 0 auto;
}

@mixin globalBorderTop {
  border-top: 4px solid $yxLightGreen;
}